import React from 'react';
import {connect} from 'react-redux';
import ButtonElement from 'components/buttons/ButtonElement';
import './style.scss';

/**
 * Absentee bid Home page.
 */
class AbsenteeBidHomeComponent extends React.Component {
  constructor(props) {
    super(props);

    this.step1url = `${this.props.extras.location.pathname}/placebid`;
  }

  render() {
    if (this.props.user.email) {
      this.props.extras.transitionTo(this.step1url);

      return null;
    }

    return (
      <div className="absentee-bid-home section-wrapper px1 clearfix">
        <h2>{this.props.placeBid}</h2>

        <div className="absentee-bid-home__choice-wrapper col col-12">
          {this.renderSections()}
        </div>
      </div>
    );
  }

  /**
   * Section to bid as a guest
   * @returns {object}
   */
  renderGuestSection = () => {
    return (
      <div className="absentee-bid-home__guest md-col md-col-6 sm-col-12" key="1">
        <h3>{this.props.noAccount}</h3>
        <ButtonElement text={this.props.guestBid}
                       buttonType="secondary"
                       className="absentee-bid-home__guest-button"
                       spaUrl={this.step1url} />
        <p>{this.props.guestNote}</p>
      </div>
    );
  };

  /**
   * Render login section.
   * @returns {object}
   */
  renderLoginSection = () => {
    const url = this.props.config.signingUrl.replace('{lang}', this.props.extras.locale);

    return (
      <div className="absentee-bid-home__registered md-col md-col-6 sm-col-12" key="2">
        <h3>{this.props.haveAccount}</h3>
        <ButtonElement
          text={this.props.login}
          buttonType="primary"
          url={url} />
      </div>
    );
  };

  /**
   * Render guest and login sections
   * @returns {object}
   */
  renderSections = () => {
    if (this.props.extras.isMobile) {
      return [this.renderLoginSection(), this.renderGuestSection()];
    }
    return [this.renderGuestSection(), this.renderLoginSection()];
  };

}

AbsenteeBidHomeComponent.propTypes = {
  extras: React.PropTypes.object,
  placeBid: React.PropTypes.string,
  noAccount: React.PropTypes.string,
  haveAccount: React.PropTypes.string,
  guestNote: React.PropTypes.string,
  guestBid: React.PropTypes.string,
  login: React.PropTypes.string,
  user: React.PropTypes.object,
  config: React.PropTypes.object
};

AbsenteeBidHomeComponent.defaultProps = {
  extras: {}
};

function mapStateToProps(state) {
  return {
    user: state.user,
    config: state.config
  };
}

export default connect(mapStateToProps, null)(AbsenteeBidHomeComponent);



