import React from 'react';
import _ from 'helpers/lodash';
import ProgressStepBar from 'components/progress-step-bar/ProgressStepBar';
import {connect} from 'react-redux';
import PriceComp from 'components/priceComp';
import LinkElement from 'components/link-element/LinkElement';
import {Field, reduxForm, startSubmit, stopSubmit} from 'redux-form';
import {renderCheckbox} from '../components/field/field';
import {postRequest, buildHeaderObject} from 'helpers/rest';
import MarkdownElement from 'components/markdown/MarkdownElement';
import './absentee-bid-step3.scss';
import {AbsenteeBidLotInfo, checkForAllDataAndTransit} from '../absentee-bid-common/LotInfo';
import {businessEntityStringProcessor} from '../helpers/utils.js';
import gtm from 'helpers/gtm';
import {classes} from 'helpers/utils';

const ABSENTEE_BID_FORM_POST_URL = '/_/o/{lotNumber}/absenteebid';

let SubmitAbsenteeBidForm = reduxForm({form: 'SubmitAbsenteeBidForm'})((props) => {
  const {handleSubmit, submitting, newsletterLabelText, commitButtonText, profile} = props;
  const receiveNewsletterField = !profile.ReceiveNewsletter && <Field name="ReceiveNewsletters" component={renderCheckbox(newsletterLabelText)} props={props} />;
  const formClassName = classes({
    'form-submitting': submitting
  });
  return <form onSubmit={handleSubmit} className={formClassName}>
    <button className="button-element primary-button button-submit" type="submit" disabled={submitting}>{commitButtonText}</button>
    {receiveNewsletterField}
  </form>;
});

export class AbsenteeBidStep3Component extends React.Component {
  commitToBid = (values) => {
    const absenteeBid = {
      ...values,
      ...this.props.absenteeBid.user,
      Bid: this.props.absenteeBid.Bid,
      IsTelephoneBid: this.props.absenteeBid.IsTelephoneBid
    };
    const {
      extras,
      extras: {
        transitionTo,
        log
      },
      data,
      placeBidUrl,
      nextStepUrl,
      errorUrl
    } = this.props;
    const lotNumber = _.get(data, 'lot.lotNumber');
    this.props.startAbsenteeBidSubmit();
    return postRequest(ABSENTEE_BID_FORM_POST_URL.replace('{lotNumber}',
      _.get(this.props, 'data.lot.lotNumber')),
      JSON.stringify(absenteeBid), buildHeaderObject())
        .catch((err) => {
          if (err.status == 400) {
            transitionTo(extras.addLocaleToUrl(placeBidUrl).replace('{lotNumber}', lotNumber));
            this.props.endAbsenteeBidSubmit({'Bid': [{msg: 'makeBidHigher'}]});
          }
          else if (err.status > 400) {
            log.error('Cannot place absentee bid', err);
            transitionTo(extras.addLocaleToUrl(errorUrl).replace('{lotNumber}', lotNumber));
          }
          return Promise.reject();
        })
        .then((res) => {
          gtm.event(gtm.ABSENTEE_BID, {
            bidValue: absenteeBid.Bid,
            absenteeBidCaseNumber: res.CaseNumber
          });
          const nextUrl = extras.addLocaleToUrl(nextStepUrl).replace('{lotNumber}', lotNumber);
          this.props.endAbsenteeBidSubmit({});
          transitionTo(nextUrl);
        });
  };

  render() {
    if (!checkForAllDataAndTransit(this.props)) return null;

    const {
        title,
        currentBidText,
        termsText,
        extras,
        data,
        yourPersonalDataText,
        prevStepUrl,
        backLinkText,
        notes,
        absenteeBid
      } = this.props;
    const lotNumber = _.get(this.props, 'data.lot.lotNumber');
    const businessEntity = _.get(data, 'auction.businessEntity', 'BER');
    const currency = _.get(data, 'lot.startingBid.currency', 'EUR');
    const previousUrl = extras.addLocaleToUrl(prevStepUrl).replace('{lotNumber}', lotNumber);
    const country = _.get(absenteeBid, 'user.CountryName', '');
    const stateName = _.get(absenteeBid, 'user.StateName', null);
    const termsTextProcessed = businessEntityStringProcessor(termsText, businessEntity);
    const notesProcessed = businessEntityStringProcessor(notes, businessEntity);

    return (
      <div className="absentee-bid-step3 section-wrapper px1 clearfix">
        <h2 className="show-mobile">{title}</h2>
        <ProgressStepBar steps={this.props.progressBarSteps.content.dataBag} currentStep={3} />
        <h2 className="show-desktop">{title}</h2>
        <div className="absentee-bid-step3__wrapper">
          <div className="absentee-bid-step3__info md-col md-col-6">
            <AbsenteeBidLotInfo {...this.props} />
            <div className="absentee-bid-step3__form">
              <div className="absentee-bid-step3__bid" ref="bidInfo">
                {currentBidText}
                <PriceComp extras={extras} value={absenteeBid.Bid} currency={currency} />
              </div>
              <h4>{yourPersonalDataText}</h4>

              <ul>
                <li>
                  <span className="field-firstName">{absenteeBid.user.FirstName}</span>
                  <span className="field-lastName">{absenteeBid.user.LastName}</span>
                </li>
                <li>
                  <span className="field-addressPrimary">{absenteeBid.user.AddressPrimary}</span>
                  <span className="field-addressSecondary">{absenteeBid.user.AddressSecondary}</span>
                </li>
                <li>
                  <span className="field-zip">{absenteeBid.user.Zip}</span>
                  <span className="field-city">{absenteeBid.user.City}</span>
                </li>
                <li >
                  <span className="field-country">{country}</span>
                  <span className="field-state">{stateName}</span>
                </li>
                <li className="field-email">{absenteeBid.user.Email}</li>
                <li className="field-phone">{absenteeBid.user.Phone}</li>
              </ul>

              <LinkElement spaLink={previousUrl} useDefaultIcon label={backLinkText} className="link--back" />
              <div className="absentee-bid-step3__terms">
                <MarkdownElement text={termsTextProcessed} />
              </div>
              <SubmitAbsenteeBidForm onSubmit={this.commitToBid} {...this.props} />
            </div>

          </div>
          <div className="absentee-bid-step3__notes md-col md-col-6">
            <MarkdownElement text={notesProcessed} />
          </div>
        </div>
      </div>
    );
  }
}

AbsenteeBidStep3Component.propTypes = {
  extras: React.PropTypes.object,
  absenteeBid: React.PropTypes.object,
  notes: React.PropTypes.string,
  data: React.PropTypes.object,
  config: React.PropTypes.object,
  startAbsenteeBidSubmit: React.PropTypes.func,
  endAbsenteeBidSubmit: React.PropTypes.func,
  title: React.PropTypes.string,
  currentBidText: React.PropTypes.string,
  locale: React.PropTypes.string,
  startingPriceText: React.PropTypes.string,
  newsletterLabelText: React.PropTypes.string,
  termsText: React.PropTypes.string,
  yourPersonalDataText: React.PropTypes.string,
  progressBarSteps: React.PropTypes.object,
  nextStepUrl: React.PropTypes.string,
  prevStepUrl: React.PropTypes.string,
  errorUrl: React.PropTypes.string,
  placeBidUrl: React.PropTypes.string,
  backLinkText: React.PropTypes.string,
  commitButtonText: React.PropTypes.string
};

AbsenteeBidStep3Component.defaultProps = {
  extras: {},
  progressBarSteps: {
    content: {
      dataBag: []
    }
  }
};

function mapStateToProps(state) {
  return {
    locale: state.modules.module.locale,
    config: state.config,
    data: state.modules.data,
    absenteeBid: state.absenteeBid,
    profile: state.user.profile
  };
}

function mapDispatchToProps(dispatch) {
  return {
    startAbsenteeBidSubmit: () => {
      dispatch(startSubmit('AbsenteeBidStepForm'));
    },
    endAbsenteeBidSubmit: (errors) => {
      dispatch(stopSubmit('AbsenteeBidStepForm', errors));
    }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AbsenteeBidStep3Component);



