import {connect} from 'react-redux';
import React, {Component}  from 'react';
import {Field, reduxForm} from 'redux-form';
import './user-info-form.scss';
import trueEmailValidator from '../lot-question/trueEmailValidator';
import {renderInput, renderSelect} from '../components/field/field';
import _ from 'helpers/lodash';
import {userProfileValidator} from 'helpers/validators';
import {isMobile} from 'helpers/device-detect';
import {fieldClass, fieldTooltip} from 'helpers/utils';

export class UserInfoFormComponent extends Component {
  render() {
    const {
        handleSubmit,
        salutationLabelText,
        firstNameLabelText,
        lastNameLabelText,
        phoneNumberLabelText,
        emailLabelText,
        salutationOptions,
        streetAddressLabelText,
        postCodeLabelText,
        cityLabelText,
        salutationPlaceholder,
        countryPlaceholder,
        countryText,
        emailPlaceholder,
        phoneNumberPlaceholder,
        confirmButton,
        countries,
        statePlaceholder,
        stateLableText,
        showStatesList,
        states
      } = this.props;
    const salutations = [
      {
        key: '',
        value: salutationPlaceholder
      }
    ].concat(salutationOptions);
    const countriesList = [
      {
        key: '',
        value: countryPlaceholder
      }
    ].concat(countries);
    const statesList = [
      {
        key: '',
        value: statePlaceholder
      }
    ].concat(states);

    /**
     * Wrapper which allows to handle submit errors outside and scroll to the first occurred error
     * @param {Object} event Submit event
     */
    const submitFn = (event) => {
      const submissionResult = handleSubmit(event);

      const errorMessage = document.getElementsByClassName('message-error')[0];
      if (errorMessage && isMobile()) {
        errorMessage.scrollIntoViewIfNeeded();
      }
      return submissionResult;
    };

    return (
      <form onSubmit={submitFn} className="user-info-form" name="UserInfoForm">
        <div className="col-6">
          <label>
            <span>{salutationLabelText}</span>
            <Field name="Salutation" component={renderSelect} props={{...this.props, selectOptions: salutations}} defaultValue="" />
          </label>
        </div>
        <div className="sm-col sm-col-12">
          <label>
            <span>{firstNameLabelText}</span>
            <Field name="FirstName" component={renderInput} props={this.props} />
          </label>
        </div>
        <div className="sm-col sm-col-12">
          <label>
            <span>{lastNameLabelText}</span>
            <Field name="LastName" component={renderInput} props={this.props} />
          </label>
        </div>
        <div className="sm-col sm-col-12">
          <label>
            <span>{streetAddressLabelText}</span>
            <Field name="AddressPrimary" component={renderInput} props={this.props} />
          </label>
          <Field name="AddressSecondary" component={renderInput} props={this.props} />
        </div>
        <div className="clearfix">
          <div className="user-info-form__zip col col-4">
            <label>
              <span>{postCodeLabelText}</span>
              <Field name="Zip" component={renderInput} props={this.props} />
            </label>
          </div>
          <div className="user-info-form__city col col-8">
            <label>
              <span>{cityLabelText}</span>
              <Field name="City" component={renderInput} props={this.props} />
            </label>
          </div>
        </div>
        <div className="sm-col sm-col-12">
          <label>
            <span>{countryText}</span>
            <Field name="CountryCode" component={renderSelect} props={{ ...this.props, selectOptions: countriesList}} />
          </label>
        </div>
        <div className="sm-col sm-col-12" style={{display: showStatesList ? 'block' : 'none'}}>
          <label>
            <span>{stateLableText}</span>
            <Field name="StateCode" component={renderSelect} props={{ ...this.props, selectOptions: statesList}} />
          </label>
        </div>
        <div className="sm-col sm-col-12">
          <label className={fieldClass(this.props.user.email)} title={fieldTooltip(this.props.user.email, this.props.emailCantBeChanged)}>
            <span>{emailLabelText}</span>
            <Field name="Email" type="email" component={renderInput} placeholder={emailPlaceholder}
                   disabled={!!this.props.user.email} props={this.props} />
          </label>
        </div>
        <div className="sm-col sm-col-12">
          <label>
            <span>{phoneNumberLabelText}</span>
            <Field name="Phone" type="tel" component={renderInput} placeholder={phoneNumberPlaceholder} props={this.props} />
          </label>
        </div>
        <button type="submit" className="user-info-form__confirm button-element disable-select primary-button">
          {confirmButton}
        </button>
      </form>
    );
  }
}

UserInfoFormComponent.propTypes = {
  handleSubmit: React.PropTypes.func,
  firstNameLabelText: React.PropTypes.string,
  lastNameLabelText: React.PropTypes.string,
  phoneNumberLabelText: React.PropTypes.string,
  phoneNumberPlaceholder: React.PropTypes.string,
  emailLabelText: React.PropTypes.string,
  salutationLabelText: React.PropTypes.string,
  salutationOptions: React.PropTypes.array,
  mrSalutationText: React.PropTypes.string,
  mrsSalutationText: React.PropTypes.string,
  requiredFieldError: React.PropTypes.string,
  isValidEmailFieldError: React.PropTypes.string,
  isTrueEmailFieldError: React.PropTypes.string,
  streetAddressLabelText: React.PropTypes.string,
  postCodeLabelText: React.PropTypes.string,
  cityLabelText: React.PropTypes.string,
  salutationPlaceholder: React.PropTypes.string,
  countryPlaceholder: React.PropTypes.string,
  countryText: React.PropTypes.string,
  statePlaceholder: React.PropTypes.string,
  stateLableText: React.PropTypes.string,
  emailPlaceholder: React.PropTypes.string,
  confirmButton: React.PropTypes.string,
  showStatesList: React.PropTypes.bool,
  countries: React.PropTypes.array,
  states: React.PropTypes.array,
  user: React.PropTypes.object,
  emailCantBeChanged: React.PropTypes.string
};

UserInfoFormComponent.defaultProps = {
  firstNameLabelText: '',
  lastNameLabelText: '',
  phoneNumberLabelText: '',
  phoneNumberPlaceholder: '',
  emailLabelText: '',
  requiredFieldError: '',
  isValidEmailFieldError: '',
  isTrueEmailFieldError: '',
  salutationLabelText: '',
  mrSalutationText: '',
  mrsSalutationText: '',
  streetAddressLabelText: '',
  postCodeLabelText: '',
  cityLabelText: '',
  salutationPlaceholder: '',
  countryText: '',
  countryPlaceholder: '',
  stateLableText: '',
  statePlaceholder: '',
  emailPlaceholder: '',
  confirmButton: '',
  salutationOptions: [],
  emailCantBeChanged: ''
};

function mapStateToProps(state) {
  let result = {
    showStatesList: _.get(state, 'form.UserInfoForm.values.CountryCode', '') == 'US',
    absenteeBid: state.absenteeBid,
    user: state.user
  };

  if (state.absenteeBid.user && state.absenteeBid.user.Email) {
    result.initialValues = {
      ...state.absenteeBid.user
    };
  }

  return result;
}

export default connect(mapStateToProps)(reduxForm({
  form: 'UserInfoForm',
  validate: userProfileValidator,
  asyncValidate: trueEmailValidator({msg: 'isTrueEmailFieldError'}),
  asyncBlurFields: ['Email'],
  destroyOnUnmount: false,
  returnRejectedSubmitPromise: true
})(UserInfoFormComponent));



