import React, { Component } from 'react';
import {connect} from 'react-redux';
import './absenteeBidStep2.scss';
import ProgressStepBar from 'components/progress-step-bar/ProgressStepBar';
import PriceComp from 'components/priceComp';
import MarkdownElement from 'components/markdown/MarkdownElement';
import ButtonElement from 'components/buttons/ButtonElement';
import UserInfoFormComponent from 'absentee-bid-step2/UserInfoFormComponent';
import * as AbsenteeBidActions from 'ui/actions/AbsenteeBidActions';
import { bindActionCreators } from 'redux';
import { untouch } from 'redux-form';
import _ from 'helpers/lodash';
import {AbsenteeBidLotInfo, checkForAllDataAndTransit} from '../absentee-bid-common/LotInfo';

export class AbsenteeBidStep2Component extends Component {
  componentWillUnmount() {
    this.props.untouchForm();
  }
  componentWillMount() {
    checkForAllDataAndTransit(this.props);
  }
  shouldComponentUpdate(newProps) {
    return !checkForAllDataAndTransit(newProps);
  }

  render() {
    const {
      title,
      currentBidText,
      formTitleText,
      backButton,
      notes,
      extras,
      absenteeBid,
      data
      } = this.props;
    const currency = _.get(data, 'lot.startingBid.currency', 'EUR');

    return (<div className="absentee-bid-step2 section-wrapper px1 clearfix">
      <h2 className="show-mobile">{title}</h2>
      <ProgressStepBar steps={this.props.progressBarSteps.content.dataBag} currentStep={2} />
      <h2 className="show-desktop">{title}</h2>
      <div className="absentee-bid-step2__wrapper">
        <div className="md-col md-col-6 absentee-bid-step2__info">
          <div className="absentee-bid-step-2__info">
            <AbsenteeBidLotInfo {...this.props} />
          </div>
          <div className="absentee-bid-step2__form">
            <div className="absentee-bid-step2__bid">
              {currentBidText}
              <PriceComp extras={extras} value={absenteeBid.Bid} currency={currency} />
            </div>
            <h3>{formTitleText}</h3>
            <div className="clearfix">
              <UserInfoFormComponent
                {...this.props}
                {..._.get(this.props, 'errors.content.dataBag', {})}
                countries={_.get(this.props, 'countries.content.dataBag', {})}
                states={_.get(this.props, 'states.content.dataBag', {})}
                salutationOptions={_.get(this.props, 'salutationOptions.content.dataBag', {})}
                extras={extras}
                onSubmit={this.submit}
              />
            </div>
            <ButtonElement
              clickHandler={this.handleBack}
              spaUrl={this.backUrl()}
              text={backButton}
              buttonType="secondary"
              className="absentee-bid-step2__back" />
          </div>
        </div>
        <div className="absentee-bid-step2__notes md-col md-col-6">
          <MarkdownElement text={notes} />
        </div>
      </div>
    </div>);
  }

  submit = (userData) => {
    const {
      actions,
      extras,
      nextStepUrl,
      data: {lot: {lotNumber}}
    } = this.props;
    const url = extras.addLocaleToUrl(nextStepUrl).replace('{lotNumber}', lotNumber);
    const countryList = _.get(this.props, 'countries.content.dataBag', {});
    const stateList = _.get(this.props, 'states.content.dataBag', {});

    userData.PreferredLanguage = extras.locale.split('-')[0];//don't send en-US
    userData.CountryName = _.get(_.find(countryList, 'key', userData.CountryCode), 'value', '');

    if (userData.StateCode) userData.StateName = _.find(stateList, {key: userData.StateCode}, {}).value;

    actions.confirmUserInfo(userData);
    extras.transitionTo(url);
  };

  backUrl = () => {
    const {
      extras,
      data,
      previousStepUrl
    } = this.props;
    const lotNumber = _.get(data, 'lot.lotNumber', '');

    return extras.addLocaleToUrl(previousStepUrl).replace('{lotNumber}', lotNumber);
  };

  handleBack = () => {
    this.props.untouchForm();
  };

}

AbsenteeBidStep2Component.propTypes = {
  extras: React.PropTypes.object,
  title: React.PropTypes.string,
  currentBidText: React.PropTypes.string,
  formTitleText: React.PropTypes.string,
  progressBarSteps: React.PropTypes.object,
  notes: React.PropTypes.string,
  backButton: React.PropTypes.string,
  actions: React.PropTypes.object,
  data: React.PropTypes.object,
  previousStepUrl: React.PropTypes.string,
  nextStepUrl: React.PropTypes.string,
  absenteeBid: React.PropTypes.object,
  startingPrice: React.PropTypes.string,
  errors: React.PropTypes.object,
  untouchForm: React.PropTypes.func
};

AbsenteeBidStep2Component.defaultProps = {
  progressBarSteps: {
    content: {
      dataBag: []
    }
  },
  currentBidText: '',
  formTitleText: '',
  backButton: '',
  notes: '',
  extras: {},
  previousStepUrl: '',
  nextStepUrl: '',
  startingPrice: '',
  data: {},
  absenteeBid: {}
};

function mapStateToProps(state) {
  return {
    data: state.modules.data,
    absenteeBid: state.absenteeBid,
    locale: state.modules.module.locale
  };
}

function mapDispatchToProps(dispatch) {
  return {
    untouchForm: () => {
      dispatch(untouch('UserInfoForm', 'Salutation', 'FirstName', 'LastName', 'Email', 'AddressPrimary', 'Zip', 'Phone', 'City', 'CountryCode'));
    },
    actions: bindActionCreators(AbsenteeBidActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AbsenteeBidStep2Component);



