import React from 'react';
import {connect} from 'react-redux';
import {destroy} from 'redux-form';
import ButtonElement from 'components/buttons/ButtonElement';
import MarkdownElement from 'components/markdown/MarkdownElement';
import {bindActionCreators} from 'redux';
import * as AbsenteeBidActions from 'ui/actions/AbsenteeBidActions';
import './style.scss';
class AbsenteeBidSuccessComponent extends React.Component {

  componentDidMount() {
    this.props.actions.clearUserInfo();
    this.props.actions.clearBidInfo();
    this.props.resetForm();
  }

  /**
   * Rendering of Success View
   * @returns {object}
   */
  render() {
    return (
      <div className="absentee-bid-success section-wrapper px1 clearfix">
        <h2>{this.props.successMessage}</h2>

        <div className="absentee-bid-success__sections-wrapper col col-12">
          {this.renderLeftSection()}
          {this.renderRightSection()}
        </div>
      </div>
    );
  }

  /**
   * rendering of left section
   * @returns {Object}
   */
  renderLeftSection = () => {
    const {
      absenteeBid: { IsTelephoneBid }
    } = this.props;
    const successMessage = IsTelephoneBid ? this.props.successTelephoneBiddingNotes : this.props.successNotes;

    return (
      <div className="absentee-bid-success__left md-col md-col-6 sm-col-12">
        <h3>{this.props.successNotesTitle}</h3>

        <MarkdownElement text={successMessage} />
      </div>
    );
  };

  /**
   * Rendering of right section
   * @returns {Object}
   */
  renderRightSection = () => {
    const {
      extras,
      forgotPasswordUrl,
      auctionUrl,
      data,
      nextStepsNotes,
      nextStepsRegisterAccount,
      nextStepsTitle,
      nextStepsBrowsing
    } = this.props;

    const forgotPasswordUrlString = extras.addLocaleToUrl(forgotPasswordUrl);
    const backToAuctionUrl = extras.addLocaleToUrl(
      auctionUrl.replace('{auctionNumber}', data.auction.auctionNumber).replace('{slug}', data.auction.slug));

    const markdown = this.props.user.email ? null : <MarkdownElement text={nextStepsNotes} />;
    const forgotPasswordButton = this.props.user.email ? null : <ButtonElement text={nextStepsRegisterAccount}
                                                                        buttonType="primary"
                                                                        url={forgotPasswordUrlString} />;

    return (
      <div className="absentee-bid-success__right md-col md-col-6 sm-col-12">
        <h3>{nextStepsTitle}</h3>
        {markdown}
        <div className="absentee-bid-success__right--button-block">
          {forgotPasswordButton}
          <ButtonElement text={nextStepsBrowsing}
                         buttonType="secondary"
                         className="absentee-bid-home__guest-button"
                         spaUrl={backToAuctionUrl} />
        </div>
      </div>
    );
  };

}

AbsenteeBidSuccessComponent.propTypes = {
  extras: React.PropTypes.object,
  absenteeBid: React.PropTypes.object,
  actions: React.PropTypes.object,
  successMessage: React.PropTypes.string,
  successTelephoneBiddingNotes: React.PropTypes.string,
  successNotesTitle: React.PropTypes.string,
  successNotes: React.PropTypes.string,
  nextStepsTitle: React.PropTypes.string,
  nextStepsNotes: React.PropTypes.string,
  nextStepsRegisterAccount: React.PropTypes.string,
  nextStepsBrowsing: React.PropTypes.string,
  forgotPasswordUrl: React.PropTypes.string,
  auctionUrl: React.PropTypes.string,
  user: React.PropTypes.object,
  config: React.PropTypes.object,
  resetForm: React.PropTypes.func,
  data: React.PropTypes.object
};

AbsenteeBidSuccessComponent.defaultProps = {
  extras: {},
  auctionUrl: '',
  forgotPasswordUrl: ''
};

function mapStateToProps(state) {
  return {
    user: state.user,
    config: state.config,
    data: state.modules.data,
    absenteeBid: state.absenteeBid
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(AbsenteeBidActions, dispatch),
    resetForm: () => {
      dispatch(destroy('UserInfoForm'));
    }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AbsenteeBidSuccessComponent);



