import React from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {Field, reduxForm} from 'redux-form';
import * as AbsenteeBidActions from 'ui/actions/AbsenteeBidActions';
import {renderAsString} from 'components/priceComp';
import MarkdownElement from 'components/markdown/MarkdownElement';
import {AbsenteeBidLotInfo} from 'absentee-bid-common/LotInfo';
import ProgressStepBar from '../components/progress-step-bar/ProgressStepBar';
import {renderCheckbox, renderBidInput} from '../components/field/field';
import validate from 'validate';
import {userProfileValidator} from 'helpers/validators';
import bidIncrement from 'helpers/bidIncrement';
import _ from 'helpers/lodash';
import {businessEntityStringProcessor} from 'helpers/utils';

import './style.scss';

const MAX_BID = 100000000;

/**
 * Get a price with currency for input placeholder.
 *
 * @param {object} lot lot object
 * @param {object} props react props
 * @returns {string}
 */
const getPricePlaceholder = (lot, props) => {
  const price = renderAsString(lot.startingBid.value, lot.startingBid.currency, props.extras.locale);
  return `${props.minimumAbsenteeBid} ${price}`;
};

/**
 * Should telephone bidding option show
 * @param {Object} props react props
 * @returns {boolean}
 */
const isShowTelephoneBidding = (props) => {
  const auctionStart = new Date(props.data.auction.startDate);
  auctionStart.setHours(auctionStart.getHours() - props.telephoneBiddingTurnOffTime);
  const isCloseBeforeAuction = new Date().valueOf() > auctionStart.valueOf();

  return !(props.telephoneBiddingTurnOff || isCloseBeforeAuction);
};

/**
 * Page form.
 *
 * @param {object} props react props
 * @returns {object}
 * @constructor
 */
let AbsenteeBidStepForm = (props) => {
  const lot = props.data.lot;
  const minimumBid = props.placedBid || _.get(lot, 'startingBid.value');
  const telephoneBidding = (
    <label>
      <Field name="IsTelephoneBid" props={props} component={renderCheckbox(props.telephoneBidding)} />
    </label>
  );

  const telephoneBiddingEl = isShowTelephoneBidding(props) ? telephoneBidding : null;

  return (
    <form onSubmit={props.handleSubmit} className="col-12">
      <div>
        <label>
          <span>{props.absenteeBidLabel}</span>
          <Field name="Bid" component={renderBidInput} props={props} min={minimumBid}
                 placeholder={getPricePlaceholder(lot, props)} />
        </label>
        {telephoneBiddingEl}
      </div>
      <button className="button-element primary-button" type="submit" disabled={!props.valid}>{props.confirm}</button>
    </form>
  );
};

AbsenteeBidStepForm.propTypes = {
  data: React.PropTypes.object,
  absenteeBidLabel: React.PropTypes.string,
  telephoneBidding: React.PropTypes.string,
  confirm: React.PropTypes.string,
  valid: React.PropTypes.bool,
  handleSubmit: React.PropTypes.func,
  placedBid: React.PropTypes.number
};

/**
 * Absentee bid Home page.
 */
class AbsenteeBidStep1 extends React.Component {
  /**
   * @override
   */
  constructor(props) {
    super(props);

    this.initForm();
  }

  /**
   * @override
   */
  render() {
    this.initForm();

    const AbsenteeBidStepFormComponent = this.absenteeBidStepFormComponent;
    const placedBid = this.getNextBid() || undefined; // if 0, don't show it in placeholder

    const notes = businessEntityStringProcessor(this.props.notes, _.get(this.props, 'data.lot.businessEntity'));
    return (
      <div className="absentee-bid-step-1 section-wrapper px1 clearfix">
        {this.renderHeader(this.props)}

        <div className="absentee-bid-step-1__wrapper">
          <div className="absentee-bid-step-1__info md-col md-col-6">
            <AbsenteeBidLotInfo {...this.props} />
            {AbsenteeBidStepFormComponent ?
              <AbsenteeBidStepFormComponent onSubmit={this.saveData} {...this.props} placedBid={placedBid}
                                            initialValues={{Bid: placedBid}} /> : null}
          </div>
          <div className="absentee-bid-step-1__notes md-col md-col-6">
            <MarkdownElement text={notes} />
            {this.showResaleRights()}
          </div>
        </div>

      </div>
    );
  }

  /**
   * @override
   */
  componentWillMount() {
    this.getUserPlacedBid();
  }

  /**
   * @override
   */
  componentDidMount() {
    this.initForm();
  }

  /**
   * @override
   */
  componentWillReceiveProps() {
    this.initForm();
  }

  /**
   * @override
   */
  componentWillUnmount() {
    this.absenteeBidStepFormComponent = null;
    if (this.props.absenteeBid.placedBid) delete this.props.absenteeBid.placedBid;
  }

  /**
   * Init a form with validator.
   */
  initForm() {
    const placedBid = this.getPlacedBid();
    if (placedBid === undefined || this.absenteeBidStepFormComponent) return;

    const errorMessagePlacedBid = this.props.errorMessageBidUnderPlaced.replace('{bid}', this.getNextBid());

    /*
     * Custom form validator.
     */
    const validator = validate.createValidator({
      Bid: [
        validate.required({msg: 'requiredFieldError'}),
        validate.isValidInteger({msg: 'errorOnlyNumbers'}),
        validate.greaterThanEqual(this.getPlacedBid())({errorMessage: errorMessagePlacedBid}),
        validate.greaterThan(this.props.data.lot.startingBid.value)({msg: 'errorMessageBidUnder'}),
        validate.lessThan(MAX_BID)({msg: 'maxBidError'}),
        validate.isBidValidStep({msg: 'isBidValidStep'})
      ]
    });

    /**
     * Form wrapped into redux form.
     */
    this.absenteeBidStepFormComponent = reduxForm({
      form: 'AbsenteeBidStepForm',
      validate: validator,
      destroyOnUnmount: false
    })(AbsenteeBidStepForm);
  }

  /**
   * Return user placed bid
   * @returns {number|undefined}
   */
  getPlacedBid() {
    if (this.props.absenteeBid.placedBid === null) return 0; // user not login
    if (this.props.absenteeBid.placedBid === undefined) return; // request in progress

    return _.get(this.props, 'absenteeBid.placedBid.Value');
  }

  /**
   * Return next AB value
   * @returns {number|undefined}
   */
  getNextBid() {
    return bidIncrement.up(this.getPlacedBid());
  }

  getUserPlacedBid = () => {
    return this.props.actions.getBidForCurrentUser({
      lotNumber: this.props.data.lot.lotNumber
    });
  };

  /**
   * Checks if user has all required fields
   * @returns {Boolean} result Flag with corresponding user validation result
   */
  isUserValid = () => {
    const {user, absenteeBid} = this.props;
    const validationObject = !!user.email ? {
      ...user.profile,
      Email: user.email
    } : { ...absenteeBid.user };

    return !Object.keys(userProfileValidator(validationObject)).length;
  };

  /**
   * Save user absentee bid price.
   * @param {object} bidData form data
   */
  saveData = (bidData) => {
    const {extras, nextStepUrl, nextStepUrlForLoggedInUsers, data, user, actions} = this.props;

    let url = extras.addLocaleToUrl(
      nextStepUrl.replace('{lotNumber}', data.lot.lotNumber));

    if (this.isUserValid()) {
      url = extras.addLocaleToUrl(
        nextStepUrlForLoggedInUsers.replace('{lotNumber}', data.lot.lotNumber));
    }

    if (user.email) {
      const countryList = _.get(this.props, 'countries.content.dataBag', {});
      const {profile} = user;

      actions.confirmUserInfo({
        ...profile,
        Email: user.email,
        CountryName: _.get(_.find(countryList, 'key', profile.CountryCode), 'value', '')
      });
    }

    actions.placeBid(bidData);
    extras.transitionTo(url);
  };

  /**
   * Render header based on desktop or mobile view.
   *
   * @param {object} props react props
   */
  renderHeader = (props) => {
    const progressCmp = <ProgressStepBar key="2" steps={this.props.progressBarSteps.content.dataBag} currentStep={1} />;
    const bidCmp = <h2 key="1">{props.placeBid}</h2>;

    if (props.extras.isMobile) {
      return [bidCmp, progressCmp];
    }

    return [progressCmp, bidCmp];
  };

  /**
   * Show resale rights text from contentful.
   * @returns {object}
   */
  showResaleRights = () => {
    if (_.get(this.props, 'data.lot.resaleRights')) {
      return (
        <div className="absentee-bid-step-1__notes-resale-rights">
          {this.props.resaleRights}
        </div>
      );
    }

    return null;
  };

}

AbsenteeBidStep1.propTypes = {
  extras: React.PropTypes.object,
  actions: React.PropTypes.object,
  config: React.PropTypes.object,
  data: React.PropTypes.object,
  progressBarSteps: React.PropTypes.object,
  placeBid: React.PropTypes.string,
  absenteeBid: React.PropTypes.object,
  absenteeBidLabel: React.PropTypes.string,
  confirm: React.PropTypes.string,
  notes: React.PropTypes.string,
  resaleRights: React.PropTypes.string,
  minimumAbsenteeBid: React.PropTypes.string,
  nextStepUrl: React.PropTypes.string,
  nextStepUrlForLoggedInUsers: React.PropTypes.string,
  user: React.PropTypes.object,
  telephoneBiddingTurnOffTime: React.PropTypes.number,
  telephoneBiddingTurnOff: React.PropTypes.bool,
  errorMessageBidUnderPlaced: React.PropTypes.string
};

AbsenteeBidStep1.defaultProps = {
  extras: {},
  progressBarSteps: {
    content: {
      dataBag: []
    }
  },
  resaleRights: '',
  telephoneBiddingTurnOffTime: 24,
  telephoneBiddingTurnOff: false,
  errorMessageBidUnderPlaced: '>= {bid}'
};

function mapStateToProps(state) {
  return {
    user: state.user,
    config: state.config,
    data: state.modules.data,
    absenteeBid: state.absenteeBid
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(AbsenteeBidActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AbsenteeBidStep1);



