import React from 'react';
import {connect} from 'react-redux';
import {destroy} from 'redux-form';
import ButtonElement from 'components/buttons/ButtonElement';
import BlockElement from 'block/BlockElement';
import LinkComp from 'components/linkComp';
import {bindActionCreators} from 'redux';
import * as AbsenteeBidActions from 'ui/actions/AbsenteeBidActions';
import './style.scss';

/**
 * Absentee bid Error page.
 */
class AbsenteeBidError extends React.Component {
  componentDidMount() {
    this.props.actions.clearUserInfo();
    this.props.actions.clearBidInfo();
    this.props.resetForm();
  }

  render() {
    const url = this.props.extras.addLocaleToUrl(
      this.props.backToAbsenteeBid.replace('{lotNumber}', this.props.data.lot.lotNumber));
    const biddingForms = this.props.biddingFormsBlock && <BlockElement {...this.props.biddingFormsBlock} />;

    return (
      <div className="absentee-bid-error section-wrapper px1 clearfix">
        <h2>{this.props.errorTitle}</h2>

        <div className="absentee-bid-error__wrapper col col-12">
          <div className="absentee-bid-error__error md-col md-col-6 sm-col-12">
            <h3>{this.props.errorMessageTitle}</h3>
            <p>{this.props.errorMessageBody}</p>
            {biddingForms}
          </div>

          <div className="absentee-bid-error__start-new md-col md-col-6 sm-col-12" >
            <h3>{this.props.startANewAbsenteeBidTitle}</h3>
            <p>{this.props.startANewAbsenteeBidDescription}</p>

            <ButtonElement
              text={this.props.returnToAbsenteeBid}
              buttonType="primary"
              spaUrl={url} />

            <div>
              <h3>{this.props.callSupport}</h3>
              <LinkComp link={'tel:' + this.props.phoneNumberToCall}
                        className="link link--with-icon link--phone">{this.props.phoneNumber}</LinkComp>
            </div>
          </div>

        </div>
      </div>
    );
  }

}

AbsenteeBidError.propTypes = {
  extras: React.PropTypes.object,
  data: React.PropTypes.object,
  biddingFormsBlock: React.PropTypes.object,
  actions: React.PropTypes.object,
  backToAbsenteeBid: React.PropTypes.string,
  errorTitle: React.PropTypes.string,
  errorMessageTitle: React.PropTypes.string,
  errorMessageBody: React.PropTypes.string,
  startANewAbsenteeBidTitle: React.PropTypes.string,
  startANewAbsenteeBidDescription: React.PropTypes.string,
  returnToAbsenteeBid: React.PropTypes.string,
  callSupport: React.PropTypes.string,
  phoneNumberToCall: React.PropTypes.string,
  resetForm: React.PropTypes.func,
  phoneNumber: React.PropTypes.string
};

AbsenteeBidError.defaultProps = {
  extras: {}
};

function mapStateToProps(state) {
  return {
    user: state.user,
    config: state.config,
    data: state.modules.data
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(AbsenteeBidActions, dispatch),
    resetForm: () => {
      dispatch(destroy('UserInfoForm'));
    }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AbsenteeBidError);



