import validate from 'validate';

exports.userProfileValidator = (values, props = {}) => {
  let validatorConfig = {
    Salutation: [validate.required({msg: 'requiredFieldError'})],
    FirstName: [validate.required({msg: 'requiredFieldError'})],
    LastName: [validate.required({msg: 'requiredFieldError'})],
    Email: [
      validate.required({msg: 'requiredFieldError'}),
      validate.isValidEmail({msg: 'isValidEmailFieldError'})
    ],
    AddressPrimary: [validate.required({msg: 'requiredFieldError'})],
    Zip: [validate.required({msg: 'requiredFieldError'})],
    City: [validate.required({msg: 'requiredFieldError'})],
    CountryCode: [validate.required({msg: 'requiredFieldError'})],
    Phone: [
      validate.required({msg: 'requiredFieldError'}),
      validate.isValidPhoneNumber({msg: 'invalidPhoneNumber'})
    ]
  };

  if (props.showStatesList) {
    validatorConfig.StateCode = [validate.required({msg: 'requiredFieldError'})];
  }

  return validate.createValidator(validatorConfig)(values, props);
};



