import React from 'react';
import PriceComp from 'components/priceComp';
import _ from 'helpers/lodash';

import './lot-info.scss';

/**
 * Lot image and description component.
 */
export class AbsenteeBidLotInfo extends React.Component {
  render() {
    const lot = this.props.data.lot;

    return (
      <figure className="absentee-bid-lot-info clearfix">
        <img src={_.get(lot, 'images[0].url', '')} alt="" className="md-col md-col-6" />
        <figcaption className="md-col md-col-6">
          <h4>{lot.title}</h4>

          <h5>
            <span>{this.props.startingPrice}</span>
            <PriceComp {...lot.startingBid} extras={this.props.extras} />
          </h5>
        </figcaption>
      </figure>
    );
  }
}

/**
 * Transit user to first step of AB.
 * @param {object} props react properties
 */
export function moveToFirstStep(props) {
  const lotNumber = _.get(props.data, 'lot.lotNumber', '');

  props.extras.transitionTo(props.extras.addLocaleToUrl(`/o/${lotNumber}/absenteebid/placebid`));
}

/**
 * Check if all required data is present for AB. Need to handle page reload, and check if
 * data still in memory. If it's lack of data, user is moving to the first step of AB.
 * @param {object} props react properties
 * @returns {boolean} true if all data is presented
 */
export function checkForAllDataAndTransit(props) {
  if (!_.get(props, 'absenteeBid.Bid')) {
    moveToFirstStep(props);

    return false;
  }

  return true;
}

AbsenteeBidLotInfo.propTypes = {
  data: React.PropTypes.object,
  extras: React.PropTypes.object,
  startingPrice: React.PropTypes.string
};



